<script>
  import { DeckEncoder } from "runeterra";
  import qs from 'qs'
  import CardBar from "./CardBar.svelte";

  let deckcode = "CEBQCAQDAQCQCBABCETTINQHAEBQEDAPCQPCKKAAAEAQEAYD";
  let url = window.location.href;
  let param = qs.parse(url.split('?')[1]);
  if (param.code) deckcode = param.code;
  let deck;
  decode();
  function decode() {
    deck = DeckEncoder.decode(deckcode);
  }
</script>

<style>
  :global(body) {
    /* this will apply to <body> */
    margin: 0;
    background: #654;
  }
  main {
    margin: 0 auto;
  }
  input {
    width: 100%;
  }
  @media (min-width: 640px) {
    main {
      max-width: none;
    }
  }
</style>

<input on:change={decode} bind:value={deckcode} placeholder="將代碼貼在此" />

<main>
  {#each deck as card}
    <CardBar {card} />
  {/each}
</main>
